
import { defineComponent, reactive } from 'vue'
import { useRouter } from 'vue-router'
export default defineComponent({
  setup() {
    const router = useRouter()
    const state = reactive<any>({
      collapsed: false
    })
    const handleTitle = () => {
      router.push('/main')
    }
    return {
      handleTitle,
      state
    }
  }
})
