
import { defineComponent, reactive } from 'vue'
import { UpOutlined, DownOutlined } from '@ant-design/icons-vue'
export default defineComponent({
  components: {
    UpOutlined,
    DownOutlined
  },
  setup() {
    const state = reactive<any>({
      isShow: false
    })
    return { state }
  }
})
